// @ts-ignore
import { Div, Span } from "pages/layout/components/Components";
import React from "react";
// @ts-ignore
import { useHistory } from "react-router";
import { ROUTES } from "routes/ApplicationRoutes";
// @ts-ignore
import styled from "styled-components";

// @ts-ignore
const StyledBanner = styled.section`
  ::before {
    background: url("https://www.dooney.com/dw/image/v2/BCWP_PRD/on/demandware.static/-/Sites-dooney-Library/default/dw760cb8c3/images/homepage/Slider/12Days/day_06_B2SAF1915_1105_Desktop.jpg");
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    content: " ";
    filter: brightness(0.4);
    z-index: -1;
  }
  position: relative;
  height: 80vh;
  width: 100%;
  display: grid;
  justify-content: center;
  align-content: center;
`;

// @ts-ignore
const BannerContent = styled.div`
  max-width: 30rem;
`;

// @ts-ignore
const ShopButton = styled.button`
  background: transparent;
  border: 2px solid white;
  padding: 1rem 2rem;
  cursor: pointer;

  :hover {
    transform: scale(1.1);
  }

  transition: all 0.25s;
`;

const Banner = () => {
  let history = useHistory();
  return (
    <StyledBanner>
      <BannerContent col center>
        <Span white size={3.5} light center marB1>
          Industrial design meets fashion.
        </Span>
        <ShopButton onClick={() => history.push(ROUTES.shop)}>
          <Span white light>
            Shop
          </Span>
        </ShopButton>
      </BannerContent>
    </StyledBanner>
  );
};

export default Banner;
